<template>
  <div class="modal-backdrop">
    <div class="modal" v-if="notRegLog">
      <header class="modal-header">
        Войдите в учетную запись
        <button type="button" class="btn-close" @click="close">X</button>
      </header>
      <section class="modal-body">
        <slot name="body">
          <v-btn class="btn-func" @click="openRegister">
            Регистрация
          </v-btn>
          <v-btn @click="openLogin" class="btn-func">
            Войти
          </v-btn>
        </slot>
      </section>
      <footer class="modal-footer">
        <button type="button" class="btn-func" @click="close">
          Продолжить без входа
        </button>
      </footer>
    </div>
    <RegModal
      v-if="registration"
      @openLogin="openLogin"
      @closeRegister="closeRegister"
    />
    <LogModal
      v-if="login"
      @closeLogin="closeLogin"
      @loginAccept="loginAccept"
    />
  </div>
</template>

<script>

import RegModal from '@/components/RegModal.vue';
import LogModal from '@/components/LogModal.vue';

export default {
  name: 'RegLogModal',
  components:{
    RegModal,
    LogModal,
  },
  data: () => ({
    notRegLog: true,
    registration: false,
    login: false,
    token: '',

    rules: {
      required: value => !!value || 'Заполните поле',
      min: v => v.length >= 8 || 'Минимум 8 символов'
    },
  }),
  methods: {
    close() {
      this.$emit('withoutLog'); // throw to parent function call
    },
    openLogin() {
      this.notRegLog = false;
      this.registration = false;
      this.login = true;
    },
    openRegister() {
      this.notRegLog = false;
      this.login = false;
      this.registration = true;
    },
    closeRegister(){
      this.registration = false;
      this.login =  false;
      this.notRegLog = true;
    },
    closeLogin(){
      this.registration = false;
      this.login =  false;
      this.notRegLog = true;
    },
    loginAccept(token){
      this.registration = false;
      this.login =  false;
      this.notRegLog = false;
      this.token = token;
      this.$emit('loginAccept', this.token);
    }
  }
};
</script>

<style>
.modal-backdrop {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal {
  background: #ffffff;
  box-shadow: 20px 20px 20px 20px;
  overflow-x: auto;
  display: flex;
  flex-direction: column;
}

.modal-header,
.modal-footer {
  padding: 15px;
  display: flex;
}

.modal-header {
  border-bottom: 1px solid #eeeeee;
  color: #ebac0c;
  justify-content: space-between;
}

.modal-footer {
  border-top: 1px solid #eeeeee;
  justify-content: flex-end;
}

.modal-body {
  position: relative;
  padding: 10px 10px 10px 10px;
}

.btn-close {
  border: none;
  font-size: 15px;
  cursor: pointer;
  font-weight: bold;
  color: #ebac0c;
  background: transparent;
}

.btn-func {
  color: white;
  background: #ebac0c;
  margin: 10px 20px 10px 20px;
  padding: 10px;
  border: 1px solid #ebac0c;
  border-radius: 2px;
}

.error-msg {
  color: #d10010;
}
</style>
