<template>
    <v-container
        fluid
    >
        <div
            height="70%"
            width="70%"
            class="d-flex align-content-space-around flex-wrap"
        >
            <div
                v-for="(lvl, index) in levels" :key=index
            >
                <v-btn
                    class="ma-8"
                    @click="startLevel(lvl.level)"
                >Уровень {{lvl.level}}: {{lvl.description}}</v-btn>
            </div>
        </div>
    </v-container>
</template>


<script>
export default {
  name: 'LevelsSelect',
  components: {

  },
  data: () => ({
      levels: [
          {level: 1,
          description: 'Мужская сила'},
      ],
  }),
  methods: {
      startLevel(level){
        this.$router.push({ name: 'level', params: {level: level}});
        return;
      }
  },
};
</script>