<template>
  <div class="home">
    <LevelsSelect/>
    <RegLogModal
      v-if="!isLogged && !continueWithoutLog"
      @withoutLog="withoutLog"
      @loginAccept="loginAccept"
    />
  </div>
</template>

<script>
// @ is an alias to /src
import RegLogModal from '@/components/RegLogModal.vue';
import LevelsSelect from '@/components/LevelsSelect.vue';

export default {
  name: 'gamestart',
  components: {
    RegLogModal,
    LevelsSelect,
  },
  data: () => ({
    isLogged: false,
    continueWithoutLog: false,
    token: "",
  }),
  methods: {
    withoutLog: function() {
        this.continueWithoutLog = true;
    },
    loginAccept: function(token) {
        this.isLogged = true;
        this.token = token;
        this.$store.commit('setToken', this.token);
    }
  }
};
</script>
